import { Container } from "@mui/material";
import CircleLoader from "components/atoms/CircleLoader";
import GoBack from "components/atoms/GoBack";
import Banner from "components/templates/Banner";
import BasicCard from "components/templates/BasicCard";
import { useNavigate, useParams } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import { SubCategoryService } from "services/subcategories.service";
import AddCategoryForm from "./AddSubCategoryForm";
import UpdateCategoryForm from "./UpdateSubCategoryForm";

export default function AddSubCategory() {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const { id } = useParams();

  const loading = useAppSelector((state) => state.formLoader.loading);

  const handleSubmit = (values: any) => {
    if (id)
      SubCategoryService.updateSubCategory(`${id}`, values, dispatch, navigate);
    else SubCategoryService.addSubCategory(values, dispatch, navigate);
  };

  return (
    <Container maxWidth="lg">
      <GoBack path="/subcategory-management" title="Sub Categories" />
      <Banner heading={`${id ? "Update" : "Add New"} Sub Category`}></Banner>
      <div style={{ position: "relative" }}>
        {loading && <CircleLoader />}
        <BasicCard>
          <AddCategoryForm onSubmit={handleSubmit} />
          {id && <UpdateCategoryForm id={id} />}
        </BasicCard>
      </div>
    </Container>
  );
}
