import { useEffect } from "react";
import { change } from "redux-form";
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import { SubCategoryService } from "services/subcategories.service";
import { subCategoryActions } from "redux/slices/subcategory";

export default function UpdateSubCategoryForm({ id }: any) {
  const form = "AddSubCategoryForm";
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const subCategory = useAppSelector((state) => state.subCategory.subCategory);

  useEffect(() => {
    SubCategoryService.getSubCategorybyID(`${id}`, dispatch);
    return () => {
      dispatch(subCategoryActions.setSubCategory(null));
    };
  }, [id, dispatch]);

  useEffect(() => {
    if (!subCategory || subCategory?.data === "Not Found") return;

    const { isActive, parentCategoryId, translations } = subCategory;

    let trans = [...translations];
    trans = trans.map(({ lang, translation }) => ({ lang, translation }));

    dispatch(change(form, "isActive", isActive));
    dispatch(change(form, "translations", trans));
    dispatch(change(form, "parentCategoryId", parentCategoryId));
  }, [navigate, dispatch, subCategory]);

  return null;
}
